import React, { Fragment, useRef, useState, useEffect } from "react";
import {
    SectionProjects, ContainerProjects,
    TitleProjects, Description, ContainerDescription,
    IMGContainer, Title
} from "../styles/ProjectsPageStyles"
import PokeGame from "../img/PokeGame.svg"
import {fadeInContainerWithStagger,
    fadeInDown,
    fadeinLeft} from "../styles/Animations.js"
const ProjectsPage = () => {

    // Detectar si intersectan los objetos y animarlos
    const outerRef = useRef(null);
    const [inViewport, setInViewport] = useState(true);
    useEffect(() => {
        const onChange = entries => {
            entries.forEach(entry => {
                if (entry.target === outerRef.current) {
                    if (entry.isIntersecting) {
                        setInViewport(true);
                    } else {
                        setInViewport(false);
                    }
                }
            });
        };
        const observer = new IntersectionObserver(onChange, { threshold: .1 });
        observer.observe(outerRef.current);
    }, [outerRef]);
    return (
        <Fragment>
                <SectionProjects ref={outerRef}>
                {inViewport &&
                    <ContainerProjects
                    variants={fadeInContainerWithStagger}
                    initial="hidden"
                    animate="visible" >
                         <TitleProjects
                          variants={fadeInDown}>
                            Projects
                         </TitleProjects>
                            <Description>
                                <ContainerDescription
                                variants={fadeinLeft} >
                                    <Title>
                                        <span>0.1</span>
                                        <h3>Memory Game</h3>
                                    </Title>
                                    <p>
                                        Application made with react, consuming the pokemon api.
                            </p>
                            <h5>Tap in the image to view more</h5>
                                </ContainerDescription>
                                <IMGContainer
                                variants={fadeInDown}
                                whileTap={{scale:0.9}} >
                                    <img src={PokeGame}></img>
                                </IMGContainer>
                            </Description>
                </ContainerProjects>
            } 
                </SectionProjects>
        </Fragment>
    )

}
export default ProjectsPage